import React, { Component } from 'react';
import { Row, Col, Button, Form, Spinner } from 'react-bootstrap';
import { motion } from "framer-motion";
import { debounce } from "lodash";
import Select from 'react-select';

import { _getAllOrganizations } from '../../services/axios/api';
import ArtistCard from '../../components/ArtistCard';
import BottomSlider from '../../components/uiElements/BottomSlider';
import Dropdown from '../../components/uiElements/Dropdown';
import { animateConfigs } from '../../constants';
import { SearchIcon, LocationIcon, SortIcon, ArtformIcon, SwitchIcon, ChevronDown } from '../../components/svgComponents';
import classes from './artist.module.css';
import Loading from '../../components/uiElements/Loading';
import globalClasses from '../../constants/globalStyles.module.css';

const OPTIONS = ['Mixed', 'Warli', 'Tanjore', 'Folk Art', 'Mandala'];

const DUMMY_ARTISTS = [
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
    {
        image:"https://www.biography.com/.image/t_share/MTc5OTk1MzU0Nzk4NzYxMDgy/gettyimages-858141682.jpg",
        name:"Jackie chan",
        place:"China",
        date:"13/12/2021"
    },
];

const stateOptions = [
    { value: 'tamilnadu', label: 'Tamilnadu' },
    { value: 'maharashtra', label: 'Maharashtra' },
    { value: 'kerala', label: 'Kerala' }
]

const sortOptions = [ 
    { value: 'Newest First', label: 'Newest First' }, 
    { value: 'Oldest First', label: 'Oldest First' }, 
    { value: 'Name (A-Z)', label: 'Name (A-Z)' },
    { value: 'Name (Z-A)', label: 'Name (Z-A)' }
]

export default class Artists extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            allOrganizations: [],
            activeTab:'Artists',
            searchKeyword: "",
            searchLoading: false,
            OrgGetParams: {
                sortBy: 'createdAt',
                sort: -1
            }
        }; 
    }

    componentDidMount() {

        this.getOrganizations();

    }

    shouldComponentUpdate() {
        if(this.props.history.location.pathname === '/' || this.props.history.location.pathname === '/organization') {
            return true;
        }
        return false;        
    }

    renderArtists = () => {
        return DUMMY_ARTISTS.map((artist, index) => {
            return <Col lg={3}>
                <ArtistCard
                    key={index}
                    image={artist.image}
                    name={artist.name}
                    date={artist.date}
                    status={'Active'}
                    bottomLeftTitle={
                    <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                        <div><LocationIcon size={20} color={'rgba(0, 25, 76, 0.4)'}/></div>
                        <div style={{opacity:0.8, marginLeft:2}}>{artist.place}</div>
                    </div>}
                    onClick={() => this.props.history.push('/viewartist/1235678')}
                />
            </Col>
        })
    }

    renderOrganizations = () => {
        return this.state.allOrganizations.map((organization) => {
            const date = new Date(organization.createdAt);
            const parsedDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`
            return <Col lg={3}>
                <ArtistCard
                    key={organization._id}
                    image={organization?.image}
                    name={organization?.name}
                    date={parsedDate}
                    showBadge={false}
                    bottomLeftTitle={
                    <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                        <div><LocationIcon size={20} color={'rgba(0, 25, 76, 0.4)'}/></div>
                        <div style={{opacity:0.8, marginLeft:2}}>{organization?.state}</div>
                    </div>}
                    onClick={() => this.props.history.push(`/vieworganization/${organization._id}`)}
                />
            </Col>
        })
    }

    handleSearch = (type, keyword) => {
        if(keyword.length == 0) {
            this.setState({searchKeyword: keyword});
            this.getOrganizations();
            return;
        }
        if(type === 'organization') {
            this.setState({searchKeyword: keyword});
            this.setState({searchLoading: true});
            const params = {...this.state.OrgGetParams};
            params['search'] = keyword;
            this.debouncedOrganizationSearch(params);
        }
    }

    handleFilter = (type, ) => {

    }

    getOrganizations = () => {
        const params = {...this.state.OrgGetParams};
        _getAllOrganizations(params)
        .then(res => {
            console.log(res);
            this.setState({allOrganizations:res.data.organizations, loading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({loading:false});
        })
    }

    debouncedOrganizationSearch = debounce((params) => {

        _getAllOrganizations(params)
        .then(res => {
            console.log(res);
            this.setState({allOrganizations:res.data.organizations, searchLoading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({searchLoading:false});
        })
    },500);

    onDropdownChange = (type, e) => {

        const params = {...this.state.OrgGetParams};

        if(e.label === 'Newest First') {
            params['sortBy'] = 'createdAt';
            params['sort'] = -1;
        }
        if(e.label === 'Oldest First') {
            params['sortBy'] = 'createdAt';
            params['sort'] = 1;
        }
        if(e.label === 'Name (A-Z)') {
            params['sortBy'] = 'name';
            params['sort'] = 1;
        }
        if(e.label === 'Name (Z-A)') {
            params['sortBy'] = 'name';
            params['sort'] = -1;
        }
      
        this.setState({searchLoading: true});
        this.debouncedOrganizationSearch(params);
        
    }

    render() {

        if(this.state.loading) {
            return <Loading/>;
        }

        const { activeTab, allOrganizations } = this.state;
        const { pathname } = this.props.history.location;

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}>
                    <div>
                        <div style={{display:'flex', justifyContent:'space-around', paddingBottom:5}}>
                            <span onClick={() => this.props.history.push('/')} className={classes.tabHeading} style={{opacity: activeTab === 'Artists' ? 1 : 0.7}}>
                                Artists (0)
                            </span>
                            <span onClick={() => this.props.history.push('/organization')} className={classes.tabHeading} style={{opacity: activeTab === 'Organizations' ? 1 : 0.7}}>
                                Organizations ({allOrganizations.length})
                            </span>
                        </div>
                        <BottomSlider barValue={pathname === '/' ? 0 : pathname === '/organization' ? 350 : 0}/>
                    </div>
                    <div>
                        <Button onClick={() => this.props.history.push('/createOrganization')} variant="outline-primary" className={globalClasses.outlinePrimaryBtn}>
                            New Organization
                        </Button>
                        <Button onClick={() => this.props.history.push('/createartist')} style={{width:125, height:40, marginLeft:10}} className={globalClasses.primaryBtn}>
                            New Artist
                        </Button>
                    </div>
                </div>
                <div style={{marginTop:40}}>
                    <div style={{position:'relative', marginBottom:20}}>
                        <Form.Control onChange={(e) => this.handleSearch('organization', e.target.value)} value={this.state.searchKeyword} type="text" placeholder="Search here" style={{paddingLeft:40, paddingTop:8, paddingBottom:8}} />
                        <span className={classes.searchIcon}>
                            <SearchIcon color="#BFC5D2" size={24}/>
                        </span>
                        {this.state.searchLoading && <span style={{position:'absolute', top:10, right:20}} >
                            <Spinner style={{height:25, width:25}} animation="border" />
                        </span>}
                    </div>
                    <Row style={{marginBottom:30}}>
                        <Col lg={3}>
                            {/* <Dropdown 
                                title="Location" 
                                value="All"
                                icon={<LocationIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />   */}
                            <Select
                                styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), placeholder: () => ({color:'#99A3B7', marginTop:7}), indicatorSeparator: () => ({width:0})}}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <LocationIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Location
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('state', e)}
                                // value={""}
                                options={stateOptions}
                            />
                        </Col>
                        {/* { pathname === '/' ? <>
                            <Col lg={3}>
                            <Dropdown 
                                title="Status" 
                                value="All"
                                icon={<SwitchIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />    
                        </Col>
                        <Col lg={3}>
                            <Dropdown 
                                title="Artform" 
                                value="All"
                                icon={<ArtformIcon size={20} color="#99A3B7"/>}
                                options={OPTIONS}
                            />    
                        </Col>
                        </> : null} */}
                        <Col lg={3}>
                            <Select
                                styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), placeholder: () => ({color:'#99A3B7', marginTop:7}), indicatorSeparator: () => ({width:0})}}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <SortIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Sort By
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('sort', e)}
                                // value={""}
                                options={sortOptions}
                            />  
                        </Col>
                    </Row>
                </div>
                <div>
                    <Row>
                        {pathname === '/' ? this.renderArtists() : pathname === '/organization' ? this.renderOrganizations() : this.renderArtists()}            
                    </Row>
                </div>
            </motion.div>
        )
    }
}
